import { toPayment, confirmOrder } from './orderFun';
import qrCode from '../../../static/qrcode/weapp-qrcode';
const columns = ['不想买了', '重复下单', '订单有误', '其它原因'];
export default {
	data() {
		return {
			showCode: false,
			btnIf: false,
			columns,
			showPopup: false,
			cancelRadio: '',
			tid: '',
			detail: {},
			cancel: {
				tid: '',
				cancel_reason: ''
			},
			qrcode_w: uni.upx2px(300)
		};
	},
	onLoad: function(option) {
		this.tid = option.tid;
	},
	onShow() {
		this.getDetail();
	},
	methods: {
		zitiCode(code) {
			new qrCode('qrcode', {
				text: code,
				width: this.qrcode_w,
				height: this.qrcode_w,
				colorDark: '#333333',
				colorLight: '#FFFFFF'
			});
		},
		callUp() {
			uni.makePhoneCall({
				phoneNumber: '13544410650'
			});
		},
		toEvaluate(tid) {
			this.toQueryLink('/pages/user/order/evaluate', {
				tid: tid
			});
		},
		receiptOrder(id) {
			confirmOrder(id, this.callBack);
		},
		paymentOrder(id) {
			toPayment(id);
		},
		callBack() {
			this.getDetail();
		},
		submitCancel() {
			if (this.cancelRadio === '') {
				this.showAlert('请选择取消原因');
				return;
			}
			this.$http({
				aurl: 'cancelOrder',
				data: this.cancel,
				success: res => {
					if (res.data.code === 0) {
						this.getDetail();
						this.showPopup = false;
					}
				},
				fail: error => {
					console.log(error);
				}
			});
		},
		cancelTid(tid) {
			this.cancel.tid = tid;
			this.showPopup = true;
		},
		closeModel() {
			this.cancelRadio = '';
			this.showPopup = false;
		},
		cancelChange(index) {
			this.cancelRadio = String(index.detail);
			this.cancel.cancel_reason = this.columns[index.detail];
		},
		cancelClick(index) {
			this.cancelRadio = String(index);
			this.cancel.cancel_reason = this.columns[index];
		},
		getDetail() {
			this.$http({
				aurl: 'orderDetail',
				params: {
					tid: this.tid
				},
				success: res => {
					if (res.data.code === 0) {
						this.detail = res.data.result;
						for (let i = 0; i < this.detail.trade_order.length; i++) {
							const item = this.detail.trade_order[i];
							if (item.after_sales_status) {
								this.btnIf = true;
								if (item.after_sales_status === 'SELLER_REFUSE_BUYER' || item.after_sales_status==='SELLER_SEND_GOODS') {
									this.btnIf = false;
									return;
								}
							}
						}
						if (this.detail.ziti_qrcode) {
							this.zitiCode(this.detail.ziti_qrcode);
						}
					}
				},
				fail: error => {
					console.log(error);
				}
			});
		}
	},
	mounted() {}
};
